/* ----------------------------------------
  LAZY
---------------------------------------- */
.lazy {
  opacity: 0;
  transition: opacity .3s ease
}
.lazy.is-loaded {
    opacity: 1;
  }
/* ----------------------------------------
  LABEL
---------------------------------------- */
.label {
  font-size: 1.2rem;
  color: hsla(0,0%,100%,.6);
  letter-spacing: 5px;
  margin-bottom: 1rem;
